














































































import { Vue, Component } from 'vue-property-decorator'
@Component
export default class MakeBurger extends Vue {

  get imgHeight() {
    switch (this.$vuetify.breakpoint.name) {
          case 'xs': return 220
          case 'sm': return 300
          case 'md': return 500
          case 'lg': return 600
          case 'xl': return 800
          default: return 100
    }
    
  }

  ingredients = [ 
    {
      name: "Bread",
      model: 0,
      color: 'amber',
      price: 100
    },
    {
      name: "Tomatoes",
      model: 0,
      color: 'red',
      price: 50
    },
    {
      name: "Mayonaise",
      model: 0,
      color: 'green lighten-3',
      price: 90
    },
    {
      name: "Fromage",
      model: 0,
      color: 'orange',
      price: 150
    },
    {
      name: "Meat",
      model: 0,
      color: 'brown',
      price: 500
    },
    {
      name: "Chicken",
      model: 0,
      color: 'deep-orange darken-4',
      price: 400
    }
  ];

  get totalPrice() {
    let price = 0;
    this.ingredients.forEach(val => {
      price += (val.model * val.price);
    })
    return price;
  }

  addPrice(n: number) {
    let item = this.ingredients[n];
    let model = item.model;
    model = model + 1;
    item = Object.assign({}, item, { model: model })
    Vue.set(this.ingredients, n, item);

  }

  substrPrice(n: number) {
    let item = this.ingredients[n];
    let model = item.model;
    if (model >= 1) model = model - 1;
    item = Object.assign({}, item, { model: model })
    Vue.set(this.ingredients, n, item);
  }
}
